import api from "./index";

export default {
    async getAllOders() {
        const res = await api().get(`/api/orders`)
        return res
    },
    async updatePayment(id, data) {
        const res = await api().put(`/api/orders/${id}/paid-cash`, data)
        return res
    },
    async updatestatus(data, id) {
        const res = await api().put(`/api/orders/${id}`, data)
        return res
    }
}