<template>
   <div class="table-responsive" >
		<v-data-table
			:headers="headers"
			:items="invoiceData"
			:items-per-page="15"
			:search="search"
			align='center'
		>
		<template v-slot:item.paid="{ item }" >
				
		</template>
		<template v-slot:item.paid="{ item }" >
				
		</template>
			<template v-slot:item.status="{ item }" >
                <!-- <v-select class="text-xs-sm" @change="updateService(item.invoiceId, item.status)"  :items="Departments" 
							v-model="item.status" dense></v-select> -->
				<!-- <v-btn small tile elevation="0" class="primary" @click="updateService(item.status, item.invoiceId)">status</v-btn> -->
			</template>
			<template v-slot:item.action="{ item }" >
				<!-- <v-btn small tile elevation="0" @click="openPaidStatus(item.paid,item.invoiceId)" class="primary">{{item.paid}}</v-btn> -->
				<v-btn small tile elevation="0" class="primary" @click="updateService(item.status, item.invoiceId)">update status</v-btn>
				<v-btn small tile elevation="0" @click="openPaidStatus(item.paid,item.invoiceId)" class="primary ml-2">client paid</v-btn>
				<v-btn @click="openDialog(item)" text icon color="grey" class="mr-3"   small slot="activator">
					<v-icon class="primary--text">remove_red_eye</v-icon>
				</v-btn>
				<!-- <v-btn text icon color="grey" small  @click="deleteItemFromInvoicesList(item)" slot="activator">
					<v-icon class="error--text">delete</v-icon>
				</v-btn> -->
			</template>
			
		</v-data-table>
		<v-dialog max-width="900" v-model="openLogs">
			<v-card class="py-6 px-4">
				<v-data-table
				:headers="logsHeader"
			    :items="logsData"
			    :items-per-page="15"
			    align='center'
				>
				</v-data-table>
				<div class="layout align-center justify-center ma-0 my-6">
					<v-btn class="accent" @click="openLogs=false">Close</v-btn>
				</div>
			</v-card>
		</v-dialog>
		<v-dialog max-width="600" v-model="openPaymentList">
			<v-card class="py-6 px-4">
				<v-data-table
				:headers="paymentlogsHeader"
			    :items="paymentPresData"
			    :items-per-page="15"
			    align='center'
				>
				</v-data-table>
				<div class="layout align-center justify-center ma-0 my-6">
					<v-btn class="accent" @click="openPaymentList= false">Close</v-btn>
				</div>
			</v-card>
		</v-dialog>
		<v-dialog v-model="editDialog" max-width="685px" class="pa-2">
			<v-card class="emb-FinalReceipt-wrap">
				<v-card-text class="pa-4">
					<v-flex xs12>
						<div class="text-center bg-grey px-4 py-6">
							<h4>Payment Status</h4>
							<!-- <h5 class="mb-6">Payment is successfully processsed and your order is on the way </h5> -->
							<h6 class="mb-6">Invoice ID:{{selectProdutsId}}</h6>
							<img src="/static/images/checked.png" width="64" height="64" alt="Success">
						</div>
						<div class="mt-12 mx-4">
							<v-layout row wrap mb-10 mx-0 mt-0>
								<v-flex xs12 sm6 md6 lg6 xl6 text-left pa-0 >
									<h6>Ordered by</h6>
									<!-- <p class="mb-1">Order ID : 2563883628932</p> -->
									<div class="mb-1">{{fristName}}</div>
									<p class="mb-1">{{lastName}}</p>
									<p class="mb-1">{{email}}</p>
								</v-flex>
								<v-flex xs12 sm6 md6 lg6 xl6 text-left pa-0>
									<h6>Address</h6>
									<p class="mb-1">{{city}}</p>
									<p class="mb-1">{{phone}}</p>
									<p class="mb-1">{{street}}</p>
								</v-flex>
							</v-layout>
							<div class="py-6 text-center bg-grey">
								<h4>Expected Date of Delivery</h4>
								<h3>6 hours</h3>
							</div>
							<h4 class="pt-12 mb-7 text-sm-left text-center">Your Ordered Details</h4>
							<div  id="producr">
								<div id="tittle">
								<div><h6> Product Image</h6></div>
								<div><h6>Product Name</h6></div>
								<div><h6>Quantity</h6></div>
								<div><h6>Price</h6></div>
								</div>
								<div v-for="product in selectProduts" :key="product" id="products">
									<div><img width="100" :src="product.pictures" alt=""></div>
									<p class="font-weight-bold">{{product.name}}</p>
									<div><p>{{product.quantity}}</p></div>
									<div>RWF{{product.price}}</div>
								</div>
							</div>
							
							<v-divider class="my-6"></v-divider>
							<div class="pt-6">
								<div class="layout align-center justify-space-between ma-0">
									<p>Subtotal</p>
									<span>RWF{{total}}</span>
								</div>
								<div class="layout align-center justify-space-between ma-0">
									<p>Delivery</p>
									<span>RWF 0</span>
								</div>
								<div class="layout align-center justify-space-between ma-0">
									<p>Tax(GST)</p>
									<span>RWF0</span>
								</div>
								<v-divider class="my-4"></v-divider>
								<div class="layout align-center justify-space-between ma-0">
									<h4>Total</h4>
									<h4>RWF{{total}}</h4>
								</div>
								<!-- <v-divider class="my-4"></v-divider> -->
							</div>
							<div class="layout align-center justify-center ma-0 my-6">
								<v-btn @click="paymentList" class="accent">Payment Logs</v-btn>
							</div>
							<div class="layout align-center justify-center ma-0 my-6">
								<v-btn color="primary" @click="editDialog= false">CLose</v-btn>
								<v-btn @click="checkLogs()" color="accent" class="ml-2">Track order</v-btn>
							</div>	
						</div>
					</v-flex>
				</v-card-text>
			</v-card>
		</v-dialog>
		<emb-delete-confirmation-2
			ref="deleteConfirmationDialog"
			messageTitle="Are You Sure You Want To Delete?"
			messageDescription="Are you sure you want to delete this invoice permanently?"
			@onConfirm="ondeleteItemFromInvoicesList"
			btn1="Cancel"
			btn2="Yes"
		>
		</emb-delete-confirmation-2>
		<v-dialog v-model="status" max-width="400">
		<v-card class="py-6 px-4">
			<!-- <div class="paidStatus mb-4">
				<div>
					<v-btn @click="changePaidStatus(true)" :class="{accent: paidStatusYN}">Yes</v-btn>
				</div>
				<div>
					<v-btn   :class="{accent: !paidStatusYN}">No</v-btn>
				</div>
			</div> -->
			<div class="input">
						<v-text-field label="RWF Amount" v-model="amount" id="input"  solo></v-text-field>   
					</div>
			<v-card-actions class="layout justify-center">
				<v-btn color="primary" @click="status = false">Cancel</v-btn>
				<v-btn color="accent"  @click="changePaidStatus">Save</v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>
	<v-dialog v-model="realstatus" max-width="400">
		<v-card class="py-6 px-4">
			<div class="paidStatus mb-4">
				<v-select class="text-xs-sm" v-model="selectedStatus"  :items="Departments" 
				 dense></v-select>
				<v-textarea v-model="comment" cols="3" label="Add comment"></v-textarea>
				
			</div>
			
			<v-card-actions class="layout justify-center">
				<v-btn color="primary" @click="realstatus = false">Cancel</v-btn>
				<v-btn color="accent"  @click="changeRealStatus">save</v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>
	</div>
</template>
<style  scoped>
#products{
	display: flex;
	justify-content: space-between;
	/* text-align: center; */
	align-items: center;
	margin-top: 10px;
}
#tittle{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
/* #card ,.froom{
	display: flex;
} */
/* .inpt{
	font-size: 50px;
} */
/* #producr{
	display: flex;
	justify-content: space-between;
} */
</style>
<script>
import Order from "Api/order";
import moment from "moment"
import { mapGetters } from "vuex";
export default {
	computed: {
			...mapGetters(["linksformbackend"])
		},
  props: {
    search: {
      default: null,
      type: String
    }
  },
  data() {
    return {
		Departments: [
			'SENT',
			'RECEIVED',
			'CANCELED',
			'DELIVERY-START',
			'DELIVERY-RECEIVED', 
			'ORDER-SERVED-AND-CLOSED'
        ],
      editDialog: false,
      invoiceList: null,
      headers: [
		{
          text: "Date",
          sortable: false,
          value: "date"
        },
        // {
        //   text: "Invoice ID",
        //   sortable: false,
        //   value: "invoiceId"
		// },
		{
          text: "First Name",
          sortable: false,
          value: "firstName"
		},
		{
          text: "Methods",
          sortable: false,
          value: "paymentOption"
        },
        {
          text: "Total Amount",
          sortable: false,
          value: "total"
		},
		{
          text: "Total Amount Paid",
          sortable: false,
          value: "totalAmountPaid"
		},
		{
          text: "Delivery",
          sortable: false,
          value: "delivery"
		},
		{
          text: "Status",
          sortable: false,
          value: "statushesd"
		},
		{
          text: "Action",
          sortable: false,
          value: "action"
        },
	  ],
	  logsHeader:[
		  {
			  text:'Time',
			  sortable: false,
			  value:'time'
		  },
		  {
			  text:'Name',
			  sortable: false,
			  value:'name'
		  },
		  {
			  text:'Role',
			  sortable: false,
			  value:'role'
		  },
		  {
			  text:'Action',
			  sortable: false,
			  value:'action'
		  },
		  {
			  text:'Comment',
			  sortable: false,
			  value:'comment'
		  }
	  ],
	paymentlogsHeader:[
		  {
			  text:'Time',
			  sortable: false,
			  value:'time'
		  },
		  {
			  text:'Method',
			  sortable: false,
			  value:'method'
		  },
		  {
			  text:'Amount',
			  sortable: false,
			  value:'amount'
		  },
	  ],
      selectDeletedItem: null,
	  invoiceData: [],
	selectProduts: [],
	selectProdutsId:'',
	pictures:'',
	name: '',
	total: '',
	fristName: '',
	lastName: '',
	city: '',
	phone: '',
	street: '',
	status: false,
	Picked: '',
	paidStatusId:'',
	paidStatusYN: false,
	realstatus: false,
	tryLogs: [],
	realTime: [],
	realTimeId:'',
	selectedStatus:'',
	comment:'',
	amount:'',
	openLogs: false,
	logsData:[],
	openPaymentList: false,
	paymentlogsData:[],
	paymentPresData:[],
	email:''
    };
  },
  async created() {
	// this.getInvoice();

	try {
		const res = await Order.getAllOders()
		// console.log(res)
		res.data.data.forEach(el => {
			console.log(el)
			const dat = moment(el.updatedAt)
			const time = moment(el.updatedAt)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
			this.tryLogs=el.logs
			this.invoiceData.push({
			  date: date,
		      invoiceId: el._id,
			  streetNumber: el.streetNumber,
			  city: el.city,
			  firstName: el.firstName,
			  lastName:el.lastName,
			  paid: el.paid,
			  paidStatusYN:el.paid,
			  status: el.status,
			  total: el.totalAmmount,
			  paymentOption: el.paymentOption,
			  agentCode: el.agentCode,
			  country: el.country,
			  email: el.email,
			  products: el.products,
			  totalAmmount: el.totalAmmount,
			  pictures:el.pic1,
			  logsddd:el.logs,
			  paymentLogs: el.paymentLogs,
			  delivery: el.delivery,
			  totalAmountPaid: el.totalAmountPaid,
			  statushesd:el.status.status,
			  phoneNumber:el.phoneNumber
			  })
			//   console.log(el)
			  
			// //   console.log()
		});
		// console.log(this.invoiceData[15].logsddd)
	} catch (err) {
		console.log(err)
	}
  },
  methods: {
    openDialog(item) {
	console.log(item)
	this.tryLogs= item.logsddd
	this.paymentlogsData=item.paymentLogs
	this.selectProdutsId=item.invoiceId
	  this.selectProduts = []
	  let price = 0
	//   this.logsData= item.logs
	  item.products.forEach(el =>{
		//   console.log(el)
		 this.selectProduts.push({
			pictures:this.linksformbackend+el._id.pictures.pic1,
			quantity: 1,
			price: el._id.price,
			name: el._id.name.en
		 })
		 price = price+ el._id.price
		 this.total = price
	  })
	//   this.selectProdutsId= item._id._id
	  this.fristName= item.firstName,
	  this.lastName = item.lastName,
	  this.city= item.city,
	  this.phone= item.phoneNumber
	  this.street = item.streetNumber
	  this.email= item.email
	//   console.log(this.phone)
	  this.editDialog = true;
	//   this.selectProduts={
	// 	  id: item.id,
	// 	  date:item.date,
	// 	  total: item.totalAmmount,
	// 	  fristName:item,
	// 	  pictures: this.pictures,
	// 	  name: this.name
	//   }
    },
    deleteItemFromInvoicesList(item) {
      this.$refs.deleteConfirmationDialog.openDialog();
      this.selectDeletedItem = item;
    },
    ondeleteItemFromInvoicesList() {
      this.$refs.deleteConfirmationDialog.close();
      let index = this.invoiceData.indexOf(this.selectDeletedItem);
      this.invoiceData.splice(index, 1);
	},
	openPaidStatus(paid, id){
		this.paidStatusId= id
		this.status= true
		this.paidStatusYN=paid
	},
    async changePaidStatus(){
		// this.paidStatusYN= order
		// console.log(order)
        try {
		// if (this.amount >= 0) {
			const res = await Order.updatePayment(this.paidStatusId, this.amount)
            this.$snotify.success(`${res.data.message}`,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 3000,
					showProgressBar:false,
			});
		// }
            
        } catch (err) {
            console.log(err.message)
        }
	},
	
	// async upadateStatus() {
	// 	try {
	// 		console.log(this.Picked)
	// 	} catch (err) {
	// 		console.log(err.message)
	// 	}
	// },
	async changeRealStatus(){
		try {
			// this.realTime.forEach(el =>{
			// 	if (Department.name === el.name) {
			// 		el.color= true
			// 	}else if (Department.name !== el.name) {
			// 		el.color= false
			// 	}
			// })
			// console.log(this.realTimeId)
			const res = await Order.updatestatus({status: this.selectedStatus ,coment:this.comment}, this.realTimeId)
            // console.log(res)
             this.$snotify.success(`${res.data.message}`,{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 3000,
					showProgressBar:false,
				});
		} catch (err) {
			console.log(err.response.message)
		}
	},
	async updateService(status, id){
		// console.log(id)
        try {
			// console.log(status)
			this.realstatus= true
			this.selectedStatus= status.status
			this.realTimeId= id

        } catch (err) {
            console.log(err.message)
        }
	},
	checkLogs(){
		this.openLogs= true
		// console.log(this.tryLogs)
		this.logsData=[]
		this.tryLogs.forEach(el =>{
			const dat = moment(el.time)
			const time = moment(el.time)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
			this.logsData.push({
				time:date,
				name: el.name,
				role:el.role,
				action:el.action,
				comment:el.action,
			})
		})
		// console.log(this.logsData)
	},
	paymentList(){
		// console.log(this.paymentlogsData)
		this.paymentPresData= []
		this.paymentlogsData.forEach(el =>{
			const dat = moment(el.updatedAt)
			const time = moment(el.updatedAt)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
			this.paymentPresData.push({
				time:date,
				method:el.method,
				amount:el.amount
			})
		})
		this.openPaymentList= true
	}
  }
};
</script>
<style  scoped>
.radioBtn{
	/* display: block;
	justify-content: center; */
	text-align: center;
	/* margin-left: 30px; */
	line-height: 30px;
}
.inpt{
	/* margin-right: 10px;
	margin-top: 9px; */
	position: relative;
	top: 4px;
	right: 10px;
}
.ttt{
	position: relative;
	top: 4px;
	right: 8px;
}
.paidStatus{
	text-align: center;
}
.paidStatus button{
	margin-bottom: 10px;
}
</style>